// material-ui
import { makeStyles } from '@material-ui/core/styles';
import {
	Box,
	Button,
	FormControl,
	FormHelperText,
	InputLabel,
	OutlinedInput,
	Typography,
} from '@material-ui/core';

// third party
import * as yup from 'yup';
import { Formik, FormikHelpers } from 'formik';
import _get from 'lodash.get';

// project imports
import { Link, useHistory } from 'react-router-dom';
import { useAuth, useSnackBar } from '../../../../hooks';
import AnimateButton from '../../../../ui-component/extended/AnimateButton';

// style constant
const useStyles = makeStyles(theme => ({
	loginInput: {
		...theme.typography.customInput,
	},	
}));

type ForgotPasswordModel = {
	username: string;
	submit: boolean;
};
const ForgotPasswordForm = ({ ...others }) => {
	const classes = useStyles();
	const showSnackBar = useSnackBar();

	const { forgotPassword } = useAuth();
	const history = useHistory();

	const validation = yup.object().shape({
		username: yup.string().max(255).required('Username is required'),
	});
	const submitForgot = async (values: ForgotPasswordModel,
		{ setStatus, setSubmitting }: FormikHelpers<ForgotPasswordModel>) => {
		try {
			await forgotPassword(values.username);
			history.push(`/confirmationPassword/${values.username}`);
		} catch (err) {
			setStatus({ success: false });
			setSubmitting(false);
			showSnackBar({
				message: _get(err, 'message', 'Something went wrong'),
				alertSeverity: 'error',
			});
		}
	};
	return (
		<Formik
			initialValues={{
				username: '',
				submit: false,
			}}
			validationSchema={validation}
			onSubmit={submitForgot}
		>
			{({
				errors,
				handleBlur,
				handleChange,
				handleSubmit,
				isSubmitting,
				touched,
				values,
			}) => (
				<form noValidate onSubmit={handleSubmit} {...others}>
					<FormControl
						fullWidth
						error={Boolean(touched.username && errors.username)}
						className={classes.loginInput}
						variant='outlined'
					>
						<InputLabel htmlFor='outlined-adornment-username-login'>
							Username
						</InputLabel>
						<OutlinedInput
							id='outlined-adornment-username-login'
							type='text'
							value={values.username}
							name='username'
							onBlur={handleBlur}
							onChange={handleChange}
							label='username'
							inputProps={{}}
						/>
						{touched.username && errors.username && (
							<FormHelperText error id='standard-weight-helper-text-username-login'>
								{errors.username}
							</FormHelperText>
						)}
					</FormControl>
					{errors.submit && (
						<Box mt={3}>
							<FormHelperText error>{errors.submit}</FormHelperText>
						</Box>
					)}
					<Box mt={1}>
						<AnimateButton>
							<Button
								disableElevation
								disabled={isSubmitting}
								fullWidth
								size='large'
								type='submit'
								variant='contained'
								color='secondary'
							>
								Send mail
							</Button>
						</AnimateButton>
					</Box>

					<Typography
						variant="subtitle1"
						component={Link}
						to="/login"
					>
						Have an account?
					</Typography>
				</form>
			)}
		</Formik>
	);
};

export default ForgotPasswordForm;
